import Endpoint from '@/services/Endpoint'

export default {
  accountTypes (payload) {
    return Endpoint().get('/s1/account/accountType', {
      params: payload
    })
  },
  createAccountType (payload) {
    return Endpoint().post('/s1/account/accountType', payload)
  },
  modifyAccountType (payload) {
    return Endpoint().patch('/s1/account/accountType', payload)
  }
}
